import * as React from 'react';
import { navigate } from 'gatsby';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import IconButton from '@mui/material/IconButton';
import Link from '@mui/material/Link';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import SwipeableDrawer from '@mui/material/SwipeableDrawer';
import Typography from '@mui/material/Typography';
import LaunchIcon from '@mui/icons-material/Launch';
import MenuIcon from '@mui/icons-material/Menu';
import { lightTheme } from './style';
import { scrollToTop, scrollToSection } from '../utils/scroll';

const MenuLink = props => (
  <Link
    color='#222'
    underline='hover'
    {...props}
    sx={{
      margin: '0 1em',
      display: {
        sm: 'inline',
        xs: 'none'
      },
      ...props.sx
    }}
  >
    <Typography
      component='span'
      variant={props.variant || 'body2'}
      color='#222'
      sx={{
        fontWeight: 'bold'
      }}
    >
      {props.children}
    </Typography>
  </Link>
);

export const Menu = ({ height, lists = [] }) => {
  const [isOpen, setIsOpen] = React.useState(false);

  const toggleDrawer = _isOpen => event => {
    if (
      event &&
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }

    setIsOpen(_isOpen);
  };

  return (
    <AppBar
      position='fixed'
      sx={{
        ...lightTheme,
        boxShadow: 'none'
      }}
    >
      <Container>
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            height
          }}
        >
          <Box>
            <Link
              href='#top'
              underline='none'
              sx={{
                display: 'block'
              }}
              onClick={e => {
                e.preventDefault();
                scrollToTop();
              }}
            >
              <Typography
                sx={{
                  fontSize: 36,
                  fontWeight: 'bold',
                  fontFamily: ['Josefin Sans', 'sans-serif'].join(','),
                  letterSpacing: 2
                }}
              >
                ICJ
              </Typography>
            </Link>
          </Box>
          <Box>
            {lists.map(({ label, link, isExternal }, i) => {
              const isAnchor = /^#.+$/.test(link);

              return (
                <MenuLink
                  key={i}
                  href={link}
                  target={isExternal && '_blank'}
                  onClick={e => {
                    if (!isExternal) {
                      //  href 
                      e.preventDefault();
                    }

                    if (isAnchor) {
                      // 
                      scrollToSection(link.replace(/^#(.*)$/, '$1'));
                    } else {
                      //  navigate 
                      navigate(link);
                    }
                  }}
                >
                  {label}
                </MenuLink>
              );
            })}
            {lists.length > 0 && (
              <IconButton
                onClick={toggleDrawer(true)}
                sx={{
                  display: {
                    sm: 'none',
                    xs: 'inline'
                  }
                }}
              >
                <MenuIcon
                  sx={{
                    color: '#222'
                  }}
                />
              </IconButton>
            )}
          </Box>
        </Box>
      </Container>

      <SwipeableDrawer
        anchor='right'
        open={isOpen}
        onClose={toggleDrawer(false)}
        onOpen={toggleDrawer(true)}
      >
        <Box
          sx={{
            ...lightTheme,
            width: 200,
            height: '100%'
          }}
          role='presentation'
          onClick={toggleDrawer(false)}
          onKeyDown={toggleDrawer(false)}
        >
          <List>
            {lists.map(({ label, link, isExternal }, i) => {
              const isAnchor = /^#.+$/.test(link);

              return (
                <ListItemButton
                  key={i}
                  href={link}
                  target={isExternal && '_blank'}
                  onClick={e => {
                    if (!isExternal) {
                      //  href 
                      e.preventDefault();
                    }

                    if (isAnchor) {
                      // 
                      scrollToSection(link.replace(/^#(.*)$/, '$1'));
                    } else {
                      //  navigate 
                      navigate(link);
                    }
                  }}
                >
                  <ListItemText
                    primary={
                      <Typography sx={{ fontWeight: 'bold' }}>
                        {label}
                        {isExternal && (
                          <LaunchIcon sx={{ ml: 0.5, width: 10, height: 10 }} />
                        )}
                      </Typography>
                    }
                  />
                </ListItemButton>
              );
            })}
          </List>
        </Box>
      </SwipeableDrawer>
    </AppBar>
  );
};
